.training-block {
    display: grid;
    grid-template-columns: 598px 578px;
    margin-bottom: 130px;
    padding: 40px 40px 40px 0;
    border-radius: 24px;
    background-color: var(--color-white-text);
}

.training-block ul {
    list-style: none;
}

.left-block {
    padding: 52px 24px 0;
    border-radius: 0 16px 16px 0;
    background-color: var(--first-step-bg-color);
}

.container {
    padding: 24px;
    border-radius: 16px 16px 0 0;
    background-color: var(--color-white-text);
}

.container h3 {
    margin-bottom: 24px;
    line-height: 27px;
    font-weight: var(--font-weight-500);
    font-size: var(--font-size-large);
}

.right-block {
    padding: 45.5px 20px;
    padding-right: 0;
    line-height: 22px;
    font-weight: var(--font-weight-400);
    font-size: var(--font-size-medium);
}

.title h2 {
    margin-bottom: 16px;
    line-height: 50.8px;
    text-transform: uppercase;
    font-weight: var(--font-weight-500);
    font-size: var(--font-size-small-title);
}

.title p {
    margin-bottom: 60px;
}

@media screen and (width <= 1439px) {
    .training-block {
        position: relative;
        display: flex;
        flex-direction: column;
        margin-bottom: 80px;
        padding: 40px;
        padding-top: 143px;
    }

    .left-block {
        padding: 40px 39px 0;
        border-radius: 16px;
    }

    .container h3 {
        margin-bottom: 16px;
    }

    .right-block {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 24px;
        padding: 0;
        line-height: 20px;
        font-weight: var(--font-weight-400);
        font-size: var(--font-size-medium);
    }

    .title {
        position: absolute;
        top: 40px;
        width: 100%;
        text-align: center;
    }

    .title h2 {
        line-height: 43px;
        font-size: var(--font-size-large-34);
    }

    .title p {
        margin-bottom: 24px;
    }

    .right-block button {
        margin: 0 auto;
    }
}

@media screen and (width <= 767px) {
    .training-block {
        margin-bottom: 40px;
        padding: 24px 16px;
        padding-top: 121px;
        border-radius: 24px 16px;
    }

    .left-block {
        padding: 20.92px 20.37px 0;
        border-radius: 8.36px;
    }

    .container {
        padding: 12.54px 12.53px 12.53px 12.54px;
        border-radius: 8.36px 8.36px 0 0;
    }

    .container h3 {
        margin-bottom: 8.36px;
        line-height: 14px;
        font-size: var(--font-size-semi-small);
    }

    .right-block {
        line-height: 19px;
        font-size: var(--font-size-small);
    }

    .title {
        top: 24px;
        padding: 0 20px;
    }

    .title h2 {
        margin-bottom: 16px;
        line-height: 27px;
        font-size: var(--font-size-large);
    }

    .title p {
        margin-bottom: 16px;
    }
}
